.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.find-user {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.find-user FORM {
  margin-bottom: 0.5rem;
}

.find-user .form-group INPUT[type="text"]{
  margin-left: 0.2rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 0.5rem;
  width: 20rem;
}
.result .github-user .details {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  margin-top: 0.2rem;
}

.result .github-user .details IMG {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}